import { Location } from '@angular/common';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { News } from 'src/app/models/news';
import { NewsService } from 'src/app/services/news.service';
import { FormSubmitModalService } from 'src/app/services/form-submit-modal.service';
import { LoadingService } from 'src/app/services/loading.service';
import { Editor } from 'ngx-editor';

@Component({
  selector: 'app-update-news',
  templateUrl: './update-news.component.html',
  styleUrls: ['./update-news.component.scss'],
})
export class UpdateNewsComponent implements OnInit, OnDestroy {
  newsId;
  submitted = false;
  updateNewsForm: FormGroup;
  news: News;
  newsImageUrl = '';
  message;
  editor: Editor;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    public loadingService: LoadingService,
    private newsService: NewsService,
    private location: Location,
    private formModal: FormSubmitModalService,
  ) { }

  ngOnInit(): void {
    this.editor = new Editor();
    this.newsId = this.route.snapshot.params['id'] || '';
    this.initForm();
    if (this.newsId && this.newsId != "" && this.newsId != null) {
      this.populateData();
    } else {
      this.news = new News();
      this.populateFormFields(this.news);
    }
  }

  initForm() {
    this.updateNewsForm = new FormGroup({
      id: new FormControl(this.newsId),
      title: new FormControl('', [Validators.required]),
      newsType: new FormControl('', [Validators.required]),
      content: new FormControl('', [Validators.required]),
      imagesUrl: new FormControl('', []),
    });
  }

  populateData() {
    this.newsService
      .fetchById(this.newsId)
      .subscribe((res: News) => {
        this.news = res;
        this.populateFormFields(this.news);
      });
  }

  populateFormFields(news: News) {
    var imagesUrl = "";
    if (news?.imagesUrl && news?.imagesUrl.length > 0) {
      imagesUrl = news?.imagesUrl[0];
    }

    var content = "";
    if (news?.content) {
      content = news?.content;
    }

    this.updateNewsForm.patchValue({
      id: news.id,
      title: news?.title,
      newsType: news?.newsType,
      content: content,
      imagesUrl: imagesUrl,
    });
  }

  submitForm() {
    this.submitted = true;
    if (this.updateNewsForm.valid)
      this.submitData(this.updateNewsForm.value);
  }

  submitData(formData: any) {
    this.loadingService.enableLoading();

    var imagesUrl = formData.imagesUrl;
    formData.imagesUrl = this.news.imagesUrl;
    if (!formData.imagesUrl || formData.imagesUrl.length == 0) {
      formData.imagesUrl = [];
      formData.imagesUrl.push(imagesUrl);
    } else {
      formData.imagesUrl[0] = imagesUrl;
    }

    if (this.newsId && this.newsId != "" && this.newsId != null) {
      this.newsService.update(formData).subscribe(res => {
        this.router.navigate(["/admin/newsview"])
      })
    } else {
      this.newsService.create(formData).subscribe(res => {
        this.router.navigate(["/admin/newsview"])
      })
    }
  }

  updateImage(image) {
    // this.categoryImageUrl = image;
  }

  resetToDefault() {
    this.populateFormFields(this.news);
    //this.categoryImageUrl = this.category.categoryImageUrl;
  }

  goBack() {
    this.location.back();
  }

  // make sure to destory the editor
  ngOnDestroy(): void {
    this.editor.destroy();
  }
}
