import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { ProductNew } from 'src/app/models/product-new';
import { CategoryNew } from 'src/app/models/category-new';
import { CategoryType } from 'src/app/models/category-type';
import { ProductService } from 'src/app/services/product.service';
import { CategoryService } from 'src/app/services/category.service';
import { LoadingService } from 'src/app/services/loading.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import * as XLSX from 'xlsx';

@Component({
  selector: 'app-view-product',
  templateUrl: './view-product.component.html',
  styleUrls: ['./view-product.component.scss'],
})
export class ViewProductComponent implements OnInit {
  products: ProductNew[] = [];
  categories: CategoryNew[] = [];
  categoryTypes: CategoryType[] = [];
  categoryId: string;
  categoryTypeId: string;
  pageIndex = 0;
  pageSize = 10;
  fileName = 'ProductList.xlsx';

  dataSource = new MatTableDataSource<ProductNew>();

  productSubscription;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  // Specify columns tht should be rendered, must match names of matColumnDef
  // ID is not beign displayed as its not present here
  displayedColumns: string[] = [
    'name',
    'price',
    'description',
    'categoryName',
    'edit'
  ];

  constructor(private service: ProductService,
    private categoryService: CategoryService,
    private router: Router,
    public loadingService: LoadingService) { }

  ngOnInit(): void {
    this.getCategories();
    this.fetchAllProducts();
  }

  fetchAllCategories() {
    this.loadingService.enableLoading();
    this.categoryService
      .fetchAllCategories()
      .subscribe((response: CategoryNew[]) => {
        this.categories = response;
        this.loadingService.disableLoading();
      });
  }

  onChangeCategory(categoryId) {
    this.categoryId = categoryId;
    if (this.categoryId || this.categoryId == "0") {
      this.fetchAllType(categoryId);
    }
    this.categoryTypeId = "";
    this.fetchAllProducts();
  }

  onChangeCategoryType(categoryTypeId) {
    this.categoryTypeId = categoryTypeId;
    this.fetchAllProducts();
  }

  fetchAllType(categoryId) {
    this.loadingService.enableLoading();
    if (this.categoryId || this.categoryId == "0") {
      this.categoryService
        .fetchById(categoryId)
        .subscribe((response: CategoryType[]) => {
          this.categoryTypes = response;
          this.loadingService.disableLoading();
        });
    }
  }

  fetchAllProducts() {
    const pageOption = {
      size: 9999,
      page: this.pageIndex
    }
    if (this.categoryTypeId) {
      this.productSubscription = this.service.findByCategoryType(this.categoryTypeId, pageOption).subscribe((data) => {
        this.products = <ProductNew[]>data.content;
        this.dataSource.data = this.products;
      });
    } else if (this.categoryId) {
      this.productSubscription = this.service.findByCategoryId(this.categoryId, pageOption).subscribe((data) => {
        this.products = <ProductNew[]>data.content;
        this.dataSource.data = this.products;
      });
    } else {
      this.productSubscription = this.service.fetchAllProducts(pageOption).subscribe((data) => {
        this.products = <ProductNew[]>data.content;
        this.dataSource.data = this.products;
      });
    }
  }

  getCategories() {
    this.categoryService.fetchAllCategories().subscribe((res) => {
      this.categories = <CategoryNew[]>res;
    });
  }

  getCategoryName(categoryId) {
    if (this.categories) {
      var cat = this.categories.find(p => p.id == categoryId);
      if (cat) {
        return cat.name;
      }
    }
    return "";
  }

  onDelete(productNew: ProductNew) {
    if (confirm("Do you want to delete news: " + productNew.name + " ?")) {
      this.service
        .delete(productNew.id)
        .subscribe((response) => {
          this.fetchAllProducts();
          this.loadingService.disableLoading();
        });
    }
  }

  numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  }

  ngAfterViewInit() {
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
  }

  doFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  ngOnDestroy(): void {
    if (this.productSubscription) this.productSubscription.unsubscribe();
  }

  exportExcel(): void {
    const pageOption = {
      size: 9999,
      page: this.pageIndex
    }
    if (this.categoryTypeId) {
      this.productSubscription = this.service.findByCategoryType(this.categoryTypeId, pageOption).subscribe((data) => {
        this.products = <ProductNew[]>data.content;
      });
    } else if (this.categoryId) {
      this.productSubscription = this.service.findByCategoryId(this.categoryId, pageOption).subscribe((data) => {
        this.products = <ProductNew[]>data.content;
      });
    } else {
      this.productSubscription = this.service.fetchAllProducts(pageOption).subscribe((data) => {
        this.products = <ProductNew[]>data.content;
      });
    }

    const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(this.products);  
    const workbook: XLSX.WorkBook = { Sheets: { 'data': worksheet }, SheetNames: ['data'] };  
    XLSX.writeFile(workbook, this.fileName);
  }

}
