import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import { AuthModalService } from 'src/app/services/auth-modal.service';
import { AuthService } from 'src/app/services/auth.service';
import { CartConfirmModalService } from 'src/app/services/cart-confirm-modal.service';
import { CartService } from 'src/app/services/cart.service';
import { LoadingService } from 'src/app/services/loading.service';
import { ProductService } from 'src/app/services/product.service';
import { CategoryService } from 'src/app/services/category.service';
import { CategoryNew } from 'src/app/models/category-new';
import { ProductNew } from 'src/app/models/product-new';
import { CategoryType } from 'src/app/models/category-type';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss'],
})
export class ProductListComponent implements OnInit {
  productList: ProductNew[] = [];
  categories: CategoryNew[];
  category: CategoryNew;
  categoryType: CategoryType;
  categoryId?: string;
  typeId?: string;
  pageIndex = 0;
  pageSize = 10;

  constructor(
    public loadingService: LoadingService,
    private categoryService: CategoryService,
    private productService: ProductService,
    private modalService: AuthModalService,
    private authService: AuthService,
    private router: Router,
    private cartService: CartService,
    private cartConfirmModal: CartConfirmModalService,
    private activatedRoute: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe(params => {
      if (params.has('categoryId')) {
        this.categoryId = params.get('categoryId');
      }
      if (params.has('typeId')) {
        this.typeId = params.get('typeId');
      }

      this.initProducts();
      this.getCategories();
      this.getTypes();
    });

  }

  initProducts() {
    if (this.categoryId && this.typeId) {
      this.fetchProductsByType();
    } else if (this.categoryId) {
      this.fetchProductsByCategory();
    } else {
      this.fetchAllProducts();
    }
  }

  getCategories() {
    this.categoryService.fetchAllCategories().subscribe((res) => {
      this.categories = <CategoryNew[]>res;
      if (this.categoryId) {
        this.categories = this.categories.filter(p => p.id == this.categoryId);
      }
    });
  }

  getTypes() {
    if (this.categoryId && this.typeId) {
      this.categoryService.getTypesByCategory(this.categoryId).subscribe((res) => {
        var types = <CategoryType[]>res;
        this.categoryType = types.find(p => p.id == this.typeId);
      });
    }
  }

  findProducts(categoryId) {
    if (this.categoryId) {
      return this.productList;
    }
    let result = [];

    if (this.productList && this.productList.length > 0) {
      for (var i = 0; i < this.productList.length && result.length < 8; i++) {
        if (this.productList[i].categoryId == categoryId) {
          result.push(this.productList[i]);
        }
      }
    }

    return result;
  }

  addToCart(itemId) {
    let item = {
      productId: itemId,
      quantity: 1,
    };
    if (this.checkForAuthAndShowPopUp()) {
      this.loadingService.enableLoading();
      this.cartService.addToCart(item).subscribe((res) => {
        this.cartConfirmModal.open();
        this.loadingService.disableLoading();
      });
    }
  }

  checkForAuthAndShowPopUp() {
    if (this.authService.isAuthenticated()) return true;
    this.modalService.open(this.router.url);
    return false;
  }

  fetchProductsByCategory() {
    const pageOption = {
      size: 9999,
      page: this.pageIndex
    }
    this.loadingService.enableLoading();
    this.productService.findByCategoryId(this.categoryId, pageOption).subscribe((res: any) => {
      this.updateProductionList(res);
      this.loadingService.disableLoading();
    });
  }

  fetchProductsByType() {
    const pageOption = {
      size: 9999,
      page: this.pageIndex
    }
    this.loadingService.enableLoading();
    this.productService.findByCategoryType(this.typeId, pageOption).subscribe((res: any) => {
      this.updateProductionList(res);
      this.loadingService.disableLoading();
    });
  }

  fetchAllProducts() {
    const pageOption = {
      size: 9999,
      page: this.pageIndex
    }
    this.loadingService.enableLoading();
    this.productService.fetchAllProducts(pageOption).subscribe((res: any) => {
      this.updateProductionList(res);
      this.loadingService.disableLoading();
    });
  }

  updateProductionList(res) {
    this.productList = <ProductNew[]>res.content;
  }

  moveTo(categoryId) {
    this.router.navigate(["./products/" + categoryId])
  }

}
