import { Location } from '@angular/common';
import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Item } from 'src/app/models/item.model';
import { ProductNew } from 'src/app/models/product-new';
import { AuthModalService } from 'src/app/services/auth-modal.service';
import { AuthService } from 'src/app/services/auth.service';
import { CartConfirmModalService } from 'src/app/services/cart-confirm-modal.service';
import { CartService } from 'src/app/services/cart.service';
import { EventService } from 'src/app/services/event.service';
import { LoadingService } from 'src/app/services/loading.service';
import { ProductService } from 'src/app/services/product.service';
import { CartItem } from 'src/app/models/cart-item';
import * as $ from 'jquery';

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.scss'],
})
export class ProductDetailsComponent implements OnInit, AfterViewInit  {
  count = 1;
  productId;
  product: ProductNew = null;
  cartItems: CartItem[] = [];
  disabledAddCart = false;

  constructor(
    public loadingService: LoadingService,
    private router: Router,
    private location: Location,
    private productService: ProductService,
    private route: ActivatedRoute,
    public eventService: EventService,
    private authService: AuthService,
    private authModalService: AuthModalService,
    private cartService: CartService,
    private cartModal: CartConfirmModalService
  ) {}

  ngOnInit(): void {
    this.productId = this.route.snapshot.paramMap.get('productId');
    this.initializeProductDetails();
    this.loadCardItems();
  }

  initializeProductDetails() {
    this.productService.fetchById(this.productId).subscribe((res: ProductNew) => {
      this.product = res;
    }).closed;
  }

  goBack() {
    this.location.back();
  }

  increment() {
    this.count++;
  }
  decrement() {
    if (this.count <= 1) this.count = 1;
    else this.count--;
  }

  addNewItem(product) {
    this.disabledAddCart = true;
    var cartItem = this.cartItems.find(p=>p.product.id == product.id);
    if(cartItem){
      cartItem.number += this.count;
    }
    else{
      cartItem = new CartItem();
      cartItem.product = {...product}
      cartItem.number = this.count;
      this.cartItems.push(cartItem);
    }

    window.localStorage.setItem("cart-items", JSON.stringify(this.cartItems));

    $('#trigger-update-cart').trigger('click');

    setTimeout(() => {
      this.disabledAddCart = false;
        }, 3000);
  }

  redirectToCategory() {
    this.eventService.categoryChanged.next(this.product.name);
    this.router.navigateByUrl('/');
  }

  checkForAuthAndShowPopUp() {
    if (this.authService.isAuthenticated()) return true;
    this.authModalService.open(this.router.url);
    return false;
  }

  loadCardItems(){
    var data = window.localStorage.getItem("cart-items");
    if(data){
      this.cartItems = <CartItem[]>JSON.parse(data);
    }else{
      this.cartItems = [];
    }
  }
  
  // badgeColor() {
  //   if (this.product?.discountPercent <= 10) return '#FFCE03';
  //   else if (this.product?.discountPercent <= 20) return '#FD9A01';
  //   else if (this.product?.discountPercent <= 30) return '#FD6104';
  //   else if (this.product?.discountPercent <= 40) return '#FF2C05';
  //   else return '#F00505';
  // }

  numberWithCommas(product) {
    if(product && product.price)
      return product?.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");

    return "";
  }

  ngAfterViewInit() {
    return;
    $('.add-to-cart').on('click', function () {
      var cart = $('#cart-on-menu');
      var imgtodrag = $('.my-card').find("img").eq(0);
      if (imgtodrag) {
          var imgclone = imgtodrag.clone()
              .offset({
              top: imgtodrag.offset().top,
              left: imgtodrag.offset().left
          })
              .css({
              'opacity': '0.5',
                  'position': 'absolute',
                  'height': '150px',
                  'width': '150px',
                  'z-index': '100'
          })
              .appendTo($('body'))
              .animate({
              'top': cart.offset().top + 10,
                  'left': cart.offset().left + 10,
                  'width': 75,
                  'height': 75
          }, 1000, 'easeInOutExpo');

          setTimeout(function () {
              cart.effect("shake", {
                  times: 2
              }, 200);
          }, 1500);

          imgclone.animate({
              'width': 0,
                  'height': 0
          }, function () {
              $(this).detach()
          });
      }
  });
  }
}
