import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddCategoryComponent } from './components/admin/add-category/add-category.component';
import { AddProductComponent } from './components/admin/add-product/add-product.component';
import { AdminComponent } from './components/admin/admin.component';
import { UpdateNewsComponent } from './components/admin/update-news/update-news.component';
import { ViewCategoriesComponent } from './components/admin/view-categories/view-categories.component';
import { ViewNewsComponent } from './components/admin/view-news/view-news.component';
import { ViewProductComponent } from './components/admin/view-product/view-product.component';
import { AuthComponent } from './components/auth/auth.component';
import { ChangePasswordComponent } from './components/auth/change-password/change-password.component';
import { LoginComponent } from './components/auth/login/login.component';
import { RegisterComponent } from './components/auth/register/register.component';
import { NewsDetailComponent } from './components/news/news-detail/news-detail.component';
import { NewsListComponent } from './components/news/news-list/news-list.component';
import { NewsComponent } from './components/news/news.component';
import { OthersDetailComponent } from './components/others/others-detail/others-detail.component';
import { OthersListComponent } from './components/others/others-list/others-list.component';
import { OthersComponent } from './components/others/others.component';
import { AboutUsComponent } from './components/products/about-us/about-us.component';
import { CartNewComponent } from './components/products/cart-new/cart-new.component';
import { CartSummaryComponent } from './components/products/checkout/cart-summary/cart-summary.component';
import { CheckoutComponent } from './components/products/checkout/checkout.component';
import { ProductDetailsComponent } from './components/products/product-details/product-details.component';
import { ProductListComponent } from './components/products/product-list/product-list.component';
import { ProductsComponent } from './components/products/products.component';
import { AuthAccessGuard } from './guards/auth-access.guard';
import { AuthGuard } from './guards/auth.guard';
import { AccessDeniedComponent } from './shared/access-denied/access-denied.component';
import { NotFoundComponent } from './shared/not-found/not-found.component';


const routes: Routes = [
  {
    path: 'auth',
    component: AuthComponent,
    canActivate: [AuthAccessGuard],
    children: [
      {
        path: 'login',
        component: LoginComponent,
      },
      {
        path: 'register',
        component: RegisterComponent,
      },
      {
        path: 'changepassword',
        component: ChangePasswordComponent,
      },
    ],
  },
  {
    path: 'news',
    component: NewsComponent,
    children: [
      { path: '', component: NewsListComponent },
      { path: ':id', component: NewsDetailComponent }
    ],
  },
  {
    path: 'others',
    component: OthersComponent,
    children: [
      { path: '', component: OthersListComponent },
      { path: ':id', component: OthersDetailComponent }
    ],
  },
  {
    path: 'products',
    component: ProductsComponent,
    children: [
      { path: '', component: ProductListComponent },
      { path: 'cart', component: CartNewComponent },
      { path: 'about', component: AboutUsComponent },
      {
        path: 'checkout',
        component: CheckoutComponent,
        canActivate: [AuthGuard],
        children: [
          { path: 'summary', component: CartSummaryComponent },
          { path: '', redirectTo: 'shipping', pathMatch: 'full' },
        ],
      },
      { path: 'detail/:productId', component: ProductDetailsComponent },
      { path: ':categoryId/:typeId', component: ProductListComponent },
      { path: ':categoryId', component: ProductListComponent }
    ],
  },
  {
    path: 'admin',
    component: AdminComponent,
    canActivate: [AuthGuard],
    children: [
      { path: 'add', component: AddProductComponent },
      { path: 'view', component: ViewProductComponent },
      { path: 'categoryview', component: ViewCategoriesComponent },
      { path: 'categoryadd', component: AddCategoryComponent },
      { path: 'categoryedit/:categoryId/:typeId', component: AddCategoryComponent },
      { path: 'newsview', component: ViewNewsComponent },
      { path: 'newsadd', component: UpdateNewsComponent },
      { path: 'newsedit/:id', component: UpdateNewsComponent },
      { path: '', redirectTo: 'view', pathMatch: 'full' },
      { path: 'product/:id', component: AddProductComponent },
    ],
  },
  { path: 'about', redirectTo: 'products/about', pathMatch: 'full' },
  { path: '404', component: NotFoundComponent },
  { path: '403', component: AccessDeniedComponent },
  { path: '', redirectTo: 'products', pathMatch: 'full' },
  { path: '**', redirectTo: 'products' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule { }
