import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import { LoadingService } from 'src/app/services/loading.service';
import { NewsService } from 'src/app/services/news.service';
import { News } from 'src/app/models/news';
import * as $ from 'jquery';

@Component({
  selector: 'app-others-list',
  templateUrl: './others-list.component.html',
  styleUrls: ['./others-list.component.scss'],
})
export class OthersListComponent implements OnInit {
  newsList: News[] = [];
  orignalNewsList: News[] = [];
  pageIndex = 0;
  pageSize = 10;
  pages = [];

  constructor(
    public loadingService: LoadingService,
    private newsService: NewsService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.fetchAllOthers();
  }

  //newsType = 1
  fetchAllOthers() {
    this.loadingService.enableLoading();
    this.newsService
      .fetchByType(1)
      .subscribe((response: any) => {
        this.orignalNewsList = <News[]>response.content;
        this.loadingService.disableLoading();
        var numberOfPage = Math.ceil(this.orignalNewsList.length / this.pageSize);
        this.pages = Array(numberOfPage).fill(1).map((x, i) => i)
        this.changePage(1);
      });
  }

  moveTo(id) {
    this.router.navigate(["./others/" + id])
  }

  changePage(page) {
    this.pageIndex = page - 1;
    this.newsList = [];
    var start = this.pageIndex * this.pageSize;
    var end = start + this.pageSize;
    if (end > this.orignalNewsList.length) {
      end = this.orignalNewsList.length;
    }
    for (var i = start; i < end; i++) {
      this.newsList.push(this.orignalNewsList[i]);
    }
  }

  updateUrl(event) {
    event.target.src = '../../../assets/images/no-image.png';
  }

  getImageUrl(news: News) {
    if (news.imagesUrl && news.imagesUrl.length > 0) {
      return news.imagesUrl[0];
    }
  }

  getSomeContent(news: News) {
    var content = $('<div>').html(news.content).text();
    if (content.length > 500) {
      return content.substring(0, 500) + " ..."
    }
    return content;
  }

}
