import { Component, EventEmitter, Input, OnInit, Output, AfterViewInit } from '@angular/core';
import { ProductNew } from 'src/app/models/product-new';

@Component({
  selector: 'app-cart-item-card',
  templateUrl: './cart-item-card.component.html',
  styleUrls: ['./cart-item-card.component.scss'],
})
export class CartItemCardComponent implements OnInit{
  @Input() product: ProductNew;
  @Input() number: number;
  @Output()
  removeItemEvent = new EventEmitter<ProductNew>();

  constructor() {}

  ngOnInit(): void {}


  deleteProduct() {
    this.removeItemEvent.emit(this.product);
  }

  numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  }

  
}
