import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { CartItem } from 'src/app/models/cart-item';
import { ProductNew } from 'src/app/models/product-new';

@Component({
  selector: 'app-cart-new',
  templateUrl: './cart-new.component.html',
  styleUrls: ['./cart-new.component.scss'],
})
export class CartNewComponent implements OnInit {
  cartItems: CartItem[] = [];

  totalPrice: number;

  constructor(
    private location: Location,
  ) { }

  ngOnInit(): void {
    this.loadCardItems();
  }

  loadCardItems(){
    var data = window.localStorage.getItem("cart-items");
    if(data){
      this.cartItems = <CartItem[]>JSON.parse(data);
    }else{
      this.cartItems = [];
    }
    this.calculatePrice();
  }

  numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  }

  calculatePrice(){
    this.totalPrice = 0;
    for(var i =0; i<this.cartItems.length; i++){
      this.totalPrice += this.cartItems[i].number * this.cartItems[i].product.price;
    }
  }

  goBack() {
    this.location.back();
  }

  

  removeItem(product: ProductNew) {
    if(confirm("Do you want to delete product: "+product.name +" ?")){
      var index = this.cartItems.findIndex(p=>p.product.id == product.id);
      if(index >= 0){
        this.cartItems.splice(index, 1);
        window.localStorage.setItem("cart-items", JSON.stringify(this.cartItems));
        this.loadCardItems();
      }
    }
  }
}
