import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';
import { CategoryNew } from 'src/app/models/category-new';
import { CategoryType } from 'src/app/models/category-type';
import { LoadingService } from 'src/app/services/loading.service';
import { CategoryService } from 'src/app/services/category.service';

@Component({
  selector: 'app-add-category',
  templateUrl: './add-category.component.html',
  styleUrls: ['./add-category.component.scss'],
})
export class AddCategoryComponent implements OnInit {
  categoryType: CategoryType;
  categories: CategoryNew[] = [];
  categoryId: string;
  typeId: string;

  constructor(private categoryService: CategoryService,
    private route: ActivatedRoute,
    private location: Location,
    private router: Router,
    public loadingService: LoadingService) {
    this.categoryType = new CategoryType();
  }

  ngOnInit() {
    this.getParamenter();
    this.fetchAllCategories();
  }

  getParamenter() {
    this.categoryId = this.route.snapshot.params['categoryId'] || '';
    this.typeId = this.route.snapshot.params['typeId'] || '';

    if ((this.categoryId || this.categoryId == "0") && this.typeId) {
      this.loadingService.enableLoading();
      this.categoryService.fetchById(this.categoryId).subscribe((response: CategoryType[]) => {
        this.categoryType = response.find(p => p.id == this.typeId);
        this.loadingService.disableLoading();
      });
    }

  }

  fetchAllCategories() {
    this.loadingService.enableLoading();
    this.categoryService
      .fetchAllCategories()
      .subscribe((response: CategoryNew[]) => {
        this.categories = response;
        this.loadingService.disableLoading();
      });
  }

  saveCategory() {
    if (this.categoryType.id) {
      this.categoryService.updateType(this.categoryType).subscribe((data) => {
        window.localStorage.setItem("categoryId", this.categoryType.categoryId);
        this.router.navigate(["/admin/categoryview"])
      });
    }
    else {
      this.categoryService.createType(this.categoryType).subscribe((data) => {
        window.localStorage.setItem("categoryId", this.categoryType.categoryId);
        this.router.navigate(["/admin/categoryview"])
      });
    }
  }

  moveBack() {
    this.location.back();
  }
}
