import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { News } from 'src/app/models/news';
import { LoadingService } from 'src/app/services/loading.service';
import { NewsService } from 'src/app/services/news.service';

@Component({
  selector: 'app-view-news',
  templateUrl: './view-news.component.html',
  styleUrls: ['./view-news.component.scss'],
})
export class ViewNewsComponent implements OnInit {
  dataSource = new MatTableDataSource<News>();
  newsSubscription;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  // Specify columns tht should be rendered, must match names of matColumnDef
  // ID is not beign displayed as its not present here
  displayedColumns: string[] = [
    'title',
    'newsType',
    'edit',
  ];

  constructor(
    private newsService: NewsService,
    public loadingService: LoadingService
  ) {}

  ngOnInit(): void {
    this.fetchAllNews();
  }
  fetchAllNews() {
    this.loadingService.enableLoading();
    this.newsSubscription = this.newsService
      .fetchAll()
      .subscribe((response: any) => {
        this.dataSource.data = <News[]> response.content;
        this.loadingService.disableLoading();
      });
  }

  ngAfterViewInit() {
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
  }

  doFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  ngOnDestroy(): void {
    if (this.newsSubscription) this.newsSubscription.unsubscribe();
  }

  onDelete(news: News){
    if(confirm("Do you want to delete news: "+ news.title +" ?")){
      this.newsService
      .delete(news.id)
      .subscribe((response) => {
        this.fetchAllNews();
        this.loadingService.disableLoading();
      });
    }
  }
}
