import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { CategoryNew } from 'src/app/models/category-new';
import { CategoryType } from 'src/app/models/category-type';
import { ProductNew } from 'src/app/models/product-new';
import { CategoryService } from 'src/app/services/category.service';
import { ProductService } from 'src/app/services/product.service';
import { LoadingService } from 'src/app/services/loading.service';

@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.component.html',
  styleUrls: ['./add-product.component.scss']
})
export class AddProductComponent implements OnInit {

  categories: CategoryNew[] = [];
  categoryTypes: CategoryType[] = [];
  categoryId: string;
  product : ProductNew;

  constructor(private service : ProductService, 
    private categoryService : CategoryService, 
    private router : Router,
    private route : ActivatedRoute,
    private location: Location,
    private loadingService: LoadingService) {
    this.product = new ProductNew();
   }

  ngOnInit(){
    this.getParamenter();
    this.fetchAllCategories();
  }

  getParamenter(){
    var id = this.route.snapshot.params['id'] || '';

    if(id){
      this.loadingService.enableLoading();
      this.service.fetchById(id).subscribe((response: ProductNew) => {
        this.product = response;
        this.loadingService.disableLoading();
        this.onChangeCategory(this.product.categoryId, this.product.typeId);
      });
    }

  }

  fetchAllCategories() {
    this.loadingService.enableLoading();
    this.categoryService
      .fetchAllCategories()
      .subscribe((response: CategoryNew[]) => {
        this.categories = response;
        this.loadingService.disableLoading();
      });
  }

  onChangeCategory(categoryId, typeId){
    this.categoryId = categoryId;
    if(this.categoryId || this.categoryId=="0")
    {
      this.fetchAllType(categoryId);
    }
    this.product.typeId=typeId;
  }

  fetchAllType(categoryId){
    this.loadingService.enableLoading();    
    if(this.categoryId || this.categoryId=="0"){
      this.categoryService
        .fetchById(categoryId)
        .subscribe((response: CategoryType[]) => {
          this.categoryTypes = response;
          this.loadingService.disableLoading();
        });
    }
  }

  getSelectedOptionText(cId){
    this.categoryId = cId;
  }

  saveProduct(){
    if(this.product.id){
      this.product.datetime = null;
      this.service.updateProduct(this.product.id, this.product).subscribe(response => {
        this.router.navigate(['admin/view']);
      });

    }else{
      this.service.addProduct(this.product).subscribe(response => {
        this.router.navigate(['admin/view']);
      });
    }
  }

  moveBack(){
    this.location.back();
  }
}
